import React, { useEffect, useRef, useState } from 'react';
import styled from 'styled-components';
import ObjectiveList from '../components/ObjectiveList';
import DatePicker from "react-datepicker";
import { ko } from 'date-fns/esm/locale';
import "react-datepicker/dist/react-datepicker.css";
import "../styles/react-datepicker.css"
import "../styles/default.css"
import starImg from '../styles/images/star.jpg';

function Home() {
    //  
    var aLocalStorageItem = JSON.parse(window.localStorage.getItem("objectiveItem"));

    /***    ***/ 
    const inputForm = useRef();  // DOM    Hook
    const onMoveToForm = () => {
        inputForm.current.scrollIntoView({ behavior: 'smooth', block: 'start' });
    };

    /***    ***/ 
    const [input, setInput] = useState({        //input    -> 
        objectiveName: '',
        objectiveDate: '',
        challengeList: ''
    });
    const [objectives, setObjectives] = useState(            //submit      ->   
        aLocalStorageItem || []   //  OR,     
    );   
    const [objectiveId, setObjectiveId] = useState(
        aLocalStorageItem? 
            aLocalStorageItem.length !== 0 ? aLocalStorageItem[aLocalStorageItem.length -1].id +1 : 1      //       
        :1 
    );

    /***   (         )  ***/
    const { objectiveName, objectiveDate, challengeList } = input;   

    /*** Input    */
    const onChange = (e) => {              //onChange      ..
        const { name, value } = e.target; //  e.target    name  value  
        setInput({
            ...input,
            [name]: value,      // name    value  
            id: objectiveId
        });  
    }

    /***    ***/ 
    const onSubmit = (e) => {
        e.preventDefault();  //alert     , state  
        
        if(objectiveName !== '' && objectiveDate !== '' && objectiveDate !== null){
            setObjectiveId(objectiveId+1);  //ID  
            setObjectives([
                ...objectives,
                input
            ]);
            alert(`"${objectiveName}"  .`);      //  
    
            setInput({               //submit  input    
                objectiveName: '',
                objectiveDate: '',
                challengeList: ''
            });            
        }else{
            alert('    . \n   .');
        }
    }

    /** objectives    localStorage state  */
    useEffect(() => {
        localStorage.setItem("objectiveItem", JSON.stringify(objectives));
    },[objectives])
    

    /***     ***/ 
    const getDayName = (date) => {          //  
        return date.toLocaleDateString('ko-KR', {
            weekday: 'long',
        }).substr(0, 1);
    }

    const createDate = (date) => {          //      
        return new Date(new Date(date.getFullYear()
            , date.getMonth()
            , date.getDate()
            , 0
            , 0
            , 0));
    }

    return (
        <HomeBody>
            <HomeDiv>
                <TitleH1> Project <br />  :  </TitleH1>
            </HomeDiv>
            <ButtonDiv>
                <AddBtn onClick={onMoveToForm}>   </AddBtn>
            </ButtonDiv>
            <ObjectiveList objectives={objectives}></ObjectiveList>
            <FormDiv ref={inputForm}>
                <Form onSubmit={onSubmit}>
                    <FormTitle> : </FormTitle>
                    
                    <InputDiv>
                        <h2>   </h2>
                        <Label>  </Label><br/>
                        <Input type="text" name="objectiveName" value={objectiveName} onChange={onChange} /><br /><br />
                        <Label>   </Label>
                        <DatePicker name="objectiveDate" selected={objectiveDate} isClearable autoComplete='off' 
                                minDate={new Date()} //   disable
                                onChange={(date) => setInput({ ...input, "objectiveDate": date })} locale={ko} dateFormat="yyyy MM dd"
                                dayClassName={date => getDayName(createDate(date)) === '' ? "sunday" : undefined}
                            /> <h5> </h5>
                        <Label> Challenge List (* ) </Label><br/>
                        <Input2 type="text" name="challengeList" value={challengeList} onChange={onChange} placeholder = "      . "/><br />
                        
                    </InputDiv>
                    <br/>
                    <SubmitBtn> </SubmitBtn>
                </Form>
            </FormDiv>
        </HomeBody>
    );
}

export default Home;

const HomeBody = styled.div`
    text-align : center;
    font-family:NanumNeoRegular;
`

const HomeDiv = styled.div`
    height: 35vh;
    position: relative;
    overflow: hidden;
`

const ButtonDiv = styled.div`
    height: 7vh;
    position: relative;
    overflow: hidden;
`

const TitleH1 = styled.h1`
    font-family: NanumNeoExtraBold;    
    font-size: 90px;
    position: absolute;
    left: 50%;
    top: 4%;
    -webkit-transform: translateX(-50%);
    -ms-transform: translateX(-50%);
    transform: translateX(-50%);
    background: url('${starImg}') no-repeat;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-size: cover;
    background-position: center;
`

const AddBtn = styled.button`
    font-family:NanumNeoBold;
    box-shadow : 0px 2px 10px -5px #000000;
    border: none;
    border-radius: 20px;
    color: white;
    font-weight: bold;
    cursor: pointer;
    padding-left: 5rem;
    padding-right: 5rem;
    height: 2.25rem;
    font-size: 1rem;
    background: #21223C;
    &:hover {
      background: #3F416E;
    }
    &:active {
      background: #21223C;
    }
`

const FormDiv = styled.div`
    height: 100vh;
    background: #e9ecef;
    display: flex;
    justify-content : center;
`

const Form = styled.form`
    height: 600px;
    width: 500px;
    margin-top: 80px;
    background : white;
    border: 0px solid;
    border-radius: 10px;
    box-shadow : 0px 2px 8px -5px #000000;
    outline: none;
    overflow: hidden;
`;

const FormTitle = styled.div`
    background : #21223C; 
    padding: 20px;
    color: white;
    font-size: 30px;
    font-weight: bold;
    
`;

const InputDiv = styled.div`
    padding-top: 20px;
    padding-left: 20%;
    text-align: left;
    font-size: 17px;
    font-weight: bold;
`

const Label = styled.label`
    color: #595959;
    line-height: 1.5rem;
`;

const Input = styled.input`
    width: 300px;
    border: 1px solid gray;
    border-radius: 5px;
    outline: none;
    line-height: 2.5rem;
    font-size: 15px;
`;

const Input2 = styled.textarea`
    width: 300px;
    height: 90px;
    border: 1px solid gray;
    border-radius: 5px;
    outline: none;
    font-size: 15px;
    resize: none;
`;

const SubmitBtn = styled.button`
    font-family:NanumNeoBold;
    width: 305px;
    margin-left: 5px;
    box-shadow : 0px 2px 10px -5px #000000;
    border: none;
    border-radius: 5px;
    color: white;
    font-weight: bold;
    cursor: pointer;
    line-height: 2.5rem;
    font-size: 1rem;
    background: #21223C;
    &:hover {
      background: #3F416E;
    }
    &:active {
      background: #21223C;
    }
`